.header {
  display: flex;
  justify-content: space-between;
  max-width: 880px;
  margin: 0 auto;
  padding-top: 45px;
  padding-bottom: 41px;
  margin-bottom: 40px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media all and (max-width: 936px) {
  .header {
    max-width: 85%;
  }
}

@media all and (max-width: 586px) {
  .header {
    max-width: 100%;
    padding-top: 28px;
    padding-bottom: 31.6px;
    margin-bottom: 38.25px;
  }
}
